* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: $color-ligth;
}

.build-card {
    border: 1px solid $color-silver;
    min-height: 457px;
    width: 100%;
    margin-bottom: 30px;
    background: linear-gradient(-75deg, $palette-blue-gum, $palette-pink-light-gum, $palette-pink-cocktail-color, $palette-yello-sand-color, $palette-blue-water-color);
    background-size: 800%;
    animation: grad cubic-bezier(0.785, 0.135, 0.15, 0.86) 15s infinite;
}

@keyframes grad {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.animation-red {
    border: 1px solid $color-silver;
    width: 100%;
    padding-bottom: 30px;
    background: linear-gradient(-75deg, $palette-red-dark, $palette-red-midtone, $palette-red-ligth, $palette-red-two-gradient);
    background-size: 800%;
    animation: gradred cubic-bezier(0.785, 0.135, 0.15, 0.86) 15s infinite;
}

@keyframes gradred {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.animation-grey {
    border: 1px solid $color-silver;
    width: 100%;
    padding-bottom: 30px;
    background: linear-gradient(-75deg, $palette-grey-dark, $palette-grey-midtone, $palette-grey-ligth, $palette-blue-dark);
    background-size: 800%;
    animation: gradgrey cubic-bezier(0.785, 0.135, 0.15, 0.86) 15s infinite;
}

@keyframes gradgrey {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.animation-gum {
    border: 1px solid $color-silver;
    width: 100%;
    padding-bottom: 30px; // background-image: linear-gradient(to bottom, $color-silver, $color-pale-grey);
    background: linear-gradient(-75deg, $palette-blue-gum, $palette-pink-light-gum, $palette-pink-dark-gum, $palette-violet-gradient, $palette-blue-intense-gradient);
    background-size: 800%;
    animation: gradgum cubic-bezier(0.785, 0.135, 0.15, 0.86) 15s infinite;
}

@keyframes gradgum {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.animation-purple {
    border: 1px solid $color-silver;
    width: 100%;
    padding-bottom: 30px; // background-image: linear-gradient(to bottom, $color-silver, $color-pale-grey);
    background: linear-gradient(-75deg, $palette-purple-midtone-color, $palette-purple-dark-color, $palette-purple-light-color, $palette-violet-gradient, $palette-red-one-gradient);
    background-size: 800%;
    animation: gradpurple cubic-bezier(0.785, 0.135, 0.15, 0.86) 15s infinite;
}

@keyframes gradpurple {
    0% {
        background-position: 0% 50%;
    }
    50% {
        background-position: 100% 50%;
    }
    100% {
        background-position: 0% 50%;
    }
}

.reset-box {
    width: 240px;
    margin: 0 auto;
    background-color: transparent;
    a {
        text-decoration: none;
        text-transform: uppercase;
    }
    i {
        background-color: transparent;
        padding-right: 5px;
    }
}

.reset-btn {
    width: 75px;
    height: 30px;
    margin: 10px 0 20px;
    padding: 5px 10px;
    border: 1px solid $color-ligth;
    border-radius: 15px;
    color: $color-secondary;
    background-color: transparent;
    font-family: $open;
    font-size: 12px;
}

.box-card {
    border: 1px solid $color-silver;
    border-radius: 2px;
    width: 240px;
    height: 367px;
    margin: 0 auto;
    box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.2), 0 1px 3px 0 rgba(0, 0, 0, 0.35);
    background-color: $color-ligth;
}

.card-header {
    margin: 20px 0 10px;
    padding-left: 20px;
    border-left: 3px solid $palette-blue-ligth;
    font-family: $comic;
    height: 42px;
    line-height: 1.1;
}

.btn__hidden {
    display: none !important;
}

.name-person {
    font-size: 18px;
    font-weight: bold;
}

.work-space {
    font-size: 16px;
    font-weight: 400;
    color: $color-secondary;
}

.card-img {
    width: 240px;
    height: 200px;
    background-color: $palette-blue-ligth;
    background-position: top;
    background-size: cover;
    background-image: url("../images/tiger.png");
}

.pic-size {
    max-width: 100%;
}

.icons-box {
    padding: 10px 20px;
}

.list-icon-card {
    list-style: none;
    display: flex;
}

.icons-contein {
    width: 32px;
    height: 32px;
    border: 2px solid $palette-blue-ligth;
    border-radius: 50%;
    font-size: 18px;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    .icons-card {
        color: $palette-blue-dark;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
    }
}

.card-footer {
    border-top: 2px solid rgba($palette-blue-midtone, 0.2);
    padding-left: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.skills__list {
    color: $color-ligth;
    list-style: none;
    font-family: $open;
    font-size: 13px;
    display: flex;
}

.skills__item {
    justify-content: center;
    align-items: center;
    margin-right: 10px;
    padding-left: 5px;
    padding-right: 5px;
    text-transform: uppercase;
    border-radius: 4px;
}

.skills__item--bg {
    background-color: $palette-blue-midtone;
}

/*unfolds*/

.build-container {
    background-image: linear-gradient(to bottom, $color-back, $color-ligth);
}

.selectors-container {
    padding: 27px 20px;
    background-image: linear-gradient(to bottom, $color-back, $color-ligth);
}

.box-selector {
    display: flex;
    border-bottom: 1px solid $color-midtone;
    margin-bottom: 10px;
    padding-bottom: 7px;
    align-items: center;
}

// Add flex-container class to the first div-child (box-selector edit-design) of main container (selectors-container) so all containing items will be flex and positioned in columns
.flex-container {
    display: flex;
    flex-direction: column;
}

// Add title-box class to new div containing icons, title-edit, buttons PLUS the selectors' divs (those containing the customize boxes), in order to insert the selectors' divs inside the class box-selector. Thanks to this, you get that the border-bottom line will go together with the content of the section
.title-box {
    display: flex;
    width: 100%;
    align-items: center;
}

.title-edit {
    margin-left: 10px;
    font-family: $font-default;
    font-size: 18px;
    text-transform: uppercase;
}

.btn-angle {
    border: none;
    background: none;
    width: 16px;
    height: 18px;
    margin-left: auto;
    transform: scale(2.5);
    color: $color-secondary;
    -webkit-text-stroke: .1rem $color-back;
}

.btn-fold {
    display: none;
}

.fas {
    cursor: pointer;
}

.icons-edit {
    color: $color-primary;
}

