.font-ubuntu {
    font-family: $ubuntu;
}

.font-comic {
    font-family: $comic;
}

.font-montse {
    font-family: $montse;
}

//media queries
@media all and (min-width:768px) {
    .reset-box {
        padding-top: 163px;
    }
    .build-container {
        display: flex;
        flex-direction: row-reverse;
        margin: 20px 40px 20px 20px;
        justify-content: center;
    }
    .build-card {
        width: 329px;
        border-radius: 6px;
        min-height: 810px;
    }
    .box-selector {
        width: 329px;
    }
    .selectors-design,
    .selectors-font {
        grid-gap: 82px;
    }
}

@media all and (min-width:1024px) {
    .build-card {
        width: 585px;
        margin-bottom: 60px;
    }
}

//Rellena
