    }
}

input {
    padding-bottom: 6px;
    padding-left: 9px;
    padding-top: 4px;
    border: 1px solid $palette-blue-ligth;
    outline-style: none;
    width: 281px;
}

::placeholder {
    color: $color-pinkish-grey;
}

.button_ad_image {
