    outline: none;
    border: none;
    border-radius: 3px;
    font-family: $open;
    font-size: 14px;
    line-height: 20px;
    box-shadow: 0 0 4px 0 rgba(0, 0, 0, 0.32);
}

.add_image {
    display: flex;
    flex-direction: row;
}

.preview-img {
    max-width: 30px;
    max-height: 30px;
}

.preview-box {
    width: 30px;
    height: 30px;
    border: 1px solid $palette-blue-ligth;
    margin-top: 6px;
}

.checkbox_contact {
    margin-bottom: 10px;
